.App {
    text-align: left;
}

.App p {
    margin: 10px;
}

.App-logo {
    -webkit-animation-name: App-logo-spin, App-logo-blur;
            animation-name: App-logo-spin, App-logo-blur;
    -webkit-animation-duration: 5s, 2s;
            animation-duration: 5s, 2s;
    -webkit-animation-iteration-count: infinite, infinite;
            animation-iteration-count: infinite, infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    height: 80px;
}

.App-header {
    background-color: #a8a;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes App-logo-blur {
    from { -webkit-filter: blur(0px); filter: blur(0px); }
    to { -webkit-filter: blur(4px); filter: blur(4px); }
}

@keyframes App-logo-blur {
    from { -webkit-filter: blur(0px); filter: blur(0px); }
    to { -webkit-filter: blur(4px); filter: blur(4px); }
}



